.shopCategory-banner {
    display: block;
    margin: 30px auto;
    width: 82%;
}

.shopCategory-indexSort {
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}

.shopCategory-sort {
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888;
}

.shopCategory-indexSort p span {
    font-weight: 600;
}

.shopCategory-products {
    margin: 20px 170px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 80px;
}

.shopCategory-loadMore {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 150px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background-color: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}

@media (max-width: 768px) {

    .shopCategory-banner {
        display: block;
        margin: 30px auto;
        width: 100%;
    }

    .shopCategory-indexSort {
        font-size: 12px;
        margin: 0px 25px;
    }

    .shopCategory-products {
        display: block;
        margin: 20px 25px;
    }

    .shopCategory-products img{
        width: 100%;
    }
}