.Breadcrum {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #5e5e5e;
    font-size: 16px;
    font-weight: 600;
    margin: 60px 170px;
    text-transform: capitalize;
}

@media (max-width: 768px) {
    .Breadcrum {
        font-size: 11px;
        margin: 60px 25px;
    }
}